<template>
  <div>
    <Modal v-model="isPopUp" class="selStyle specialWidth" :mask-closable="false">
      <p slot="header">
        <span>实施方案信息</span>
      </p>
      <Form ref="form" :model="form" :label-width="80">
        <Tabs v-model="tabName" :animated="false" class="innerTabStyle">
          <TabPane label="基本信息" name="name1">
            <Row>
              <Col span="24">
              <FormItem label="项目名称" prop="projectName">
                <Input v-model.trim="form.projectName" disabled></Input>
              </FormItem></Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="承担单位">
                <Input v-model.trim="form.carryUnit" disabled></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="资金类别">
                <Input v-model.trim="form.capitalType" disabled></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="主管单位">
                <Input v-model.trim="form.manageUnit" disabled></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="实施地点" prop="implementSite">
                <Input v-model.trim="form.implementSite" disabled></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem label="工作内容">
                <Input v-model.trim="form.workContent" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem label="实施进度">
                <Input v-model.trim="form.implementJindu" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
              </FormItem>
              </Col>
            </Row>

          </TabPane>
          <TabPane label="资金构成" name="name2">
            <Row>
              <Col span="12">
              <FormItem label="总投资">
                <Input v-model.trim="form.allFinancial" disabled number>
                <span slot="append">万元</span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="中央财政">
                <Input v-model.trim="form.centralFinancial" disabled number>
                <span slot="append">万元</span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="市级财政">
                <Input v-model.trim="form.cityFinancial" disabled number>
                <span slot="append">万元</span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="区级财政">
                <Input v-model.trim="form.districtFinancial" disabled number>
                <span slot="append">万元</span>
                </Input>
              </FormItem>
              </Col>
              <Col span="12">
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem label="其他资金">
                <Input v-model.trim="form.elseFinancial" disabled number>
                <span slot="append">万元</span>
                </Input>
              </FormItem>
              </Col>
            </Row>
          </TabPane>
          <TabPane label="效益目标" name="name3">
            <Col span="24">
            <FormItem label="工作目标">
              <Input v-model.trim="form.workTarget" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
            </FormItem>
            </Col>
            <Col span="24">
            <FormItem label="预期效益">
              <Input v-model.trim="form.expectBenefit" disabled type="textarea" :autosize="{minRows: 1,maxRows: 5}"></Input>
            </FormItem>
            </Col>
          </TabPane>
          <TabPane class="uploadStyle" label="附件" name="name4">
            <uploadFile ref="uploadFile" is-disabled :form="{fileids: form.projectFileIds}" />
          </TabPane>
        </Tabs>
      </Form>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'InvestSummary',
  components: {
    uploadFile
  },
  props: {
    searchData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      tabName: 'name1',
      form: {
        // 项目名称
        projectName: '',
        // 实施地点
        implementSite: '',
        // 工作内容
        workContent: '',
        // 实施进度
        implementJindu: '',
        // 总投资
        allFinancial: '',
        // 中央财政
        centralFinancial: '',
        // 市级财政
        cityFinancial: '',
        // 区级财政
        districtFinancial: '',
        // 其他资金
        elseFinancial: '',
        // 工作目标
        workTarget: '',
        // 预期效益
        expectBenefit: ''
      },
      isPopUp: false
    }
  },
  mounted() {

  },
  methods: {
    search() {
      const that = this;
      that.clearFormData();
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/project/searchProject', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          if (data.length === 1) {
            that.form = data[0]
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems()
            })
            that.isPopUp = true
          } else {
            that.$Message.error('该数据可能已经被删除了');
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    clearFormData() {
      this.form = {
        // 项目名称
        projectName: '',
        // 实施地点
        implementSite: '',
        // 工作内容
        workContent: '',
        // 实施进度
        implementJindu: '',
        // 总投资
        allFinancial: '',
        // 中央财政
        centralFinancial: '',
        // 市级财政
        cityFinancial: '',
        // 区级财政
        districtFinancial: '',
        // 其他资金
        elseFinancial: '',
        // 工作目标
        workTarget: '',
        // 预期效益
        expectBenefit: ''
      }
      this.tabName = 'name1'
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>



