<template>
  <div class="crumbs">
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>{{ parentManage }}
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon>{{ childrenManage }}
      </BreadcrumbItem>
      <span class="screenContent">
        <img style="width: 11px;margin-right: 8px;margin-left: 10px;" src="../../assets/day.png">
        {{ curTime }}</span>
      <span v-if="!isHide" class="screenContent">
        <img style="width: 10.5px;margin-right: 8px" src="../../assets/screen.png">
        <a href="javascript:void(0)" @click="isShowScreen">
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
  </div>
</template>

<script>
export default {
  name: 'Crumbs',
  props: {
    parentManage: {
      type: String,
      default: () => {
        return ''
      }
    },
    childrenManage: {
      type: String,
      default: () => {
        return ''
      }
    },
    isHide: {
      type: Boolean,
      default: () => {
        return false
      }
    }
  },
  data() {
    return {
      isScreen: false,
      curTime: this.$moment(new Date()).format('YYYY-MM-DD HH:mm')
    }
  },
  methods: {
    isShowScreen() {
      this.isScreen = !this.isScreen;
      this.$emit('isShowScreen', this.isScreen);
    }
  }
}
</script>

<style scope>
</style>



