<template>
  <div>
    <div v-if="sum>0" style="text-align:right;margin-top:15px;font-size:20px">
      {{ name }}：<span style="font-size:24px;color:#2db7f5"><Icon type="logo-yen" />{{ sum }}</span>
    </div>
  </div>
</template>

<script>

export default {
  name: 'BottomTotal',
  props: {
    name: {
      type: String,
      default: ''
    },
    sum: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
    }
  },
  mounted() {

  },
  methods: {
  }
}
</script>



