<template>
  <div class="bidding">
    <Modal
      v-model="isPop"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled">请添加招投标信息</span>
        <span v-else>招投标信息</span>
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :rules="ruleValidateType"
            :label-width="85"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="招投标单位"
                prop="bidding_unit"
              >
                <Input
                  v-model.trim="form.bidding_unit"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写招投标单位"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="组织形式"
                prop="organizational_form"
              >
                <Select
                  v-model="form.organizational_form"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请选择组织形式"
                >
                  <Option v-for="(item,key) in organizationalForm" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="负责人"
                prop="person_charge"
              >
                <Input
                  v-model.trim="form.person_charge"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写负责人"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="联系电话"
                prop="phone"
              >
                <Input
                  v-model.trim="form.phone"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写联系电话"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="编号"
                prop="code"
              >
                <Input
                  v-model.trim="form.code"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写编号"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="标段名称"
                prop="bidding_name"
              >
                <Input
                  v-model.trim="form.bidding_name"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写标段名称"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="招标类型"
                prop="bidding_type"
              >
                <Select
                  v-model="form.bidding_type"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请选择招标类型"
                >
                  <Option v-for="(item,key) in billType" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="开标时间"
                prop="start_bidding_time"
              >
                <DatePicker v-model="form.start_bidding_time" :disabled="isDisabled" type="date" placeholder="请填写开标时间" style="width: 100%" transfer clearable></DatePicker>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="代理机构"
                prop="proxy_org"
              >
                <Input
                  v-model.trim="form.proxy_org"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写代理机构"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="中标单位"
                prop="winning_bid_org"
              >
                <Input
                  v-model.trim="form.winning_bid_org"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写中标单位"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="中标金额"
                prop="winning_bid_money"
              >
                <Input
                  v-model.trim="form.winning_bid_money"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写中标金额"
                  number
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="备注" prop="remark">
                <Input
                  v-model.trim="form.remark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </Form>
        </TabPane>
        <TabPane
          label="附件"
          name="name7"
        >
          <mulUploadFile ref="uploadFile" :is-disabled="isDisabled" :upload-list="uploadList" />
        </TabPane>
      </Tabs>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="handleCancel('form')"
        >
          取消
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          确认
        </Button>
      </p>
    </Modal>
  </div>
</template>

<script>
import mulUploadFile from '@/components/mulUploadFile.vue'
export default {
  name: 'Bidding',
  components: { mulUploadFile },
  props: {
    isDisabled: {
      type: Boolean,
      default: false
    },
    isCreate: {
      type: Boolean,
      default: false
    },
    organizationalForm: {
      type: Object,
      default: () => {}
    },
    billType: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      uploadList: {
        1: '招标公告',
        2: '招标文件资料',
        3: '评标报告',
        4: '中标及未中标通知书',
        5: '中标文件',
        6: '代理机构营业执照'
      },
      // 弹出框内容显示隐藏   默认隐藏
      isPop: false,
      // 添加表单的内容字段  ->  传递数据的字段
      form: {
        id: null,
        bidding_unit: '',
        person_charge: '',
        code: '',
        phone: '',
        bidding_name: '',
        organizational_form: '',
        type: '',
        start_bidding_time: '',
        proxy_org: '',
        winning_bid_org: '',
        winning_bid_money: null,
        remark: ''
      },
      // 表单内容的校验规则
      ruleValidateType: {
        bidding_unit: [
          { required: true, message: '请填写招投标单位', trigger: 'blur' }
        ],
        person_charge: [
          { required: true, message: '请填写负责人', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '请填写联系电话', trigger: 'blur' }
        ],
        bidding_name: [
          { required: true, message: '请填写标段名称', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '请填写编号', trigger: 'blur' }
        ],
        organizational_form: [
          { type: 'number', required: true, message: '请选择组织形式', trigger: 'change' }
        ],
        bidding_type: [
          { type: 'number', required: true, message: '请选择招标类型', trigger: 'change' }
        ],
        start_bidding_time: [
          { type: 'date', required: true, message: '请填写开标时间', trigger: 'blur' }
        ],
        proxy_org: [
          { required: true, message: '请填写代理机构', trigger: 'blur' }
        ],
        winning_bid_org: [
          { required: true, message: '请填写中标单位', trigger: 'blur' }
        ],
        winning_bid_money: [
          { type: 'number', required: true, message: '请填写中标金额', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 取消操作
    handleCancel(name) {
      this.$refs[name].resetFields();
      this.isPop = false;
    },
    // 确认操作   关系后台数据
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          const isCreate = that.isCreate
          const method = isCreate ? '/bidding/addBidding' : '/bidding/updateBidding'
          const form = Object.assign({}, that.form)
          form.start_bidding_time = new Date(form.start_bidding_time).getTime()
          form.relationFileIds = this.$refs.uploadFile.getFileIds()
          that.$Spin.show();
          that.commonUtil.ajaxCommonUtil(isCreate, method, form, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isPop = false
              that.$Message.success(res.msg);
              that.$emit('refreshList')
            } else {
              that.$Message.error(res.msg);
            }
          }, () => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    showModal(id) {
      const that = this;
      that.clearForm();
      if (id) {
        // this.$nextTick(() => {
        //   this.$refs.uploadFile.getFileItems()
        // })
        that.$Spin.show();
        that.commonUtil.ajaxGetUtil('/bidding/searchBiddingDetails/' + id, {}, (res) => {
          // 关闭数据加载
          that.$Spin.hide();
          if (res.code === 1) {
            const form = res.data
            form.start_bidding_time = new Date(form.start_bidding_time)
            that.form = form
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems(form.relationFileids)
              that.isPop = true
            })
          } else {
            that.$Message.error(res.msg);
          }
        }, () => {
          that.$Message.error('网络连接错误');
          // 关闭数据加载
          that.$Spin.hide();
        }, that)
      } else {
        that.isPop = true
      }
    },
    clearForm() {
      this.form = {
        id: null,
        bidding_unit: '',
        person_charge: '',
        phone: '',
        code: '',
        bidding_name: '',
        organizational_form: '',
        type: '',
        start_bidding_time: '',
        proxy_org: '',
        winning_bid_org: '',
        winning_bid_money: null,
        remark: ''
      }
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>



