<script>
import uploadFile from '@/components/fileUpload.vue'
import bottomTotal from '@/components/bottomTotal.vue'
export default {
  name: 'SetUpModel',
  components: { uploadFile, bottomTotal },
  props: {
    isDisabled: {
      type: Boolean,
      value: false
    },
    isCreate: {
      type: Boolean,
      value: false
    },
    signGist: {
      type: Object,
      default: () => {}
    },
    budgetGist: {
      type: Object,
      default: () => {}
    },
    contractAttr: {
      type: Object,
      default: () => {}
    },
    contractId: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      isAdd: false,
      tabName: 'name1',
      sumMoney: null,
      setUpModel: false,
      // 当查看详情时，添加 按钮隐藏
      isbtnShow: true,
      businessModel: false,
      editIndex: -1, // 当前聚焦的输入框的行数
      // 点击操作之后的文本框的转变
      editForm: {
        name: '',
        model: '',
        parameter: '',
        brand: '',
        producers: '',
        producingArea: '',
        number: '',
        unit: '',
        unitPrice: '',
        sumPrice: ''
      },
      entrustList: [],
      form: {
        contractId: null,
        contractType: 2,
        contractName: '',
        budgetGist: '',
        signGist: '',
        contractAttr: '',
        signDate: '',
        executeTime: '',
        yifangName: '',
        yifangAddr: '',
        yifangPhone: '',
        legalPersion: '',
        certigier: '',
        depositBank: '',
        depositBankNum: '',
        dutyParagraph: ''
      },
      businessCon: {
        name: '',
        model: '',
        parameter: '',
        brand: '',
        producers: '',
        producingArea: '',
        number: '',
        unit: '',
        unitPrice: '',
        sumPrice: ''
      },
      ruleBusinessCon: {
        name: [
          { required: true, message: '请填写商品名称', trigger: 'blur' }
        ],
        model: [
          { required: true, message: '请填写型号', trigger: 'blur' }
        ],
        parameter: [
          { required: true, message: '请填写参数', trigger: 'blur' }
        ],
        brand: [
          { required: true, message: '请填写品牌', trigger: 'blur' }
        ],
        producers: [
          { required: true, message: '请填写生产厂商', trigger: 'blur' }
        ],
        producingArea: [
          { required: true, message: '请填写产地', trigger: 'blur' }
        ],
        number: [
          { required: true, message: '请填写数量', trigger: 'blur' }
        ],
        unit: [
          { required: true, message: '请填写单位', trigger: 'blur' }
        ],
        unitPrice: [
          { required: true, message: '请填写单价', trigger: 'blur' }
        ],
        sumPrice: [
          { required: true, message: '请填写总价', trigger: 'blur' }
        ]
      },
      formType: {
        contractName: [
          { required: true, message: '请填写合同名称', trigger: 'blur' }
        ],
        budgetGist: [
          { type: 'number', required: true, message: '请选择预算依据', trigger: 'change' }
        ],
        signGist: [
          { type: 'number', required: true, message: '请填写签订依据', trigger: 'change' }
        ],
        contractAttr: [
          { type: 'number', required: true, message: '请填写合同属性', trigger: 'change' }
        ],
        signDate: [
          { type: 'date', required: true, message: '请选择签订日期', trigger: 'change' }
        ],
        executeTime: [
          { type: 'date', required: true, message: '请选择执行时间', trigger: 'change' }
        ],
        yifangName: [
          { required: true, message: '请填写乙方名称', trigger: 'blur' }
        ],
        yifangAddr: [
          { required: true, message: '请填写乙方地址', trigger: 'blur' }
        ],
        yifangPhone: [
          { required: true, message: '请填写联系电话', trigger: 'blur' }
        ],
        legalPersion: [
          { required: true, message: '请填写法人代表', trigger: 'blur' }
        ],
        certigier: [
          { required: true, message: '请填写授权人', trigger: 'blur' }
        ],
        depositBank: [
          { required: true, message: '请填写开户银行', trigger: 'blur' }
        ],
        depositBankNum: [
          { required: true, message: '请填写账号', trigger: 'blur' }
        ],
        dutyParagraph: [
          { required: true, message: '请填写税号', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    entrustColumns() {
      return [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '商品名称',
          slot: 'name'
        },
        {
          title: '型号',
          slot: 'model'
        },
        {
          title: '参数',
          slot: 'parameter'
        },
        {
          title: '品牌',
          slot: 'brand'
        },
        {
          title: '生产厂商',
          slot: 'producers'
        },
        {
          title: '产地',
          slot: 'producingArea'
        },
        {
          title: '数量',
          slot: 'number'
        },
        {
          title: '单位',
          slot: 'unit'
        },
        {
          title: '单价',
          slot: 'unitPrice'
        },
        {
          title: '总价',
          slot: 'sumPrice'
        },
        {
          align: 'center',
          title: '操作',
          slot: 'action',
          width: '130'
        }
      ]
    }
  },
  methods: {
    getNewForm() {
      const form = Object.assign({}, this.form)
      form.signDate = new Date(form.signDate).getTime()
      form.executeTime = new Date(form.executeTime).getTime()
      form.fileIds = this.$refs.uploadFile.getFileIds()
      return form
    },
    entrustListAdd() {
      // 需要做判断
      const that = this;
      // 填写好设备购置合同的基本信息
      that.$refs.form.validate((valid) => {
        if (valid) {
          if (that.form.contractId) {
            that.businessModel = true;
            that.clearBusinessCon();
          } else {
            // 调用接口，添加
            const form = that.getNewForm()
            that.$Spin.show();
            that.commonUtil.ajaxPostUtil('/contract/addContract', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.isAdd = true
                that.form.contractId = res.data
                that.clearBusinessCon();
                that.businessModel = true;
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请先填写设备购置合同的基本信息！');
        }
      })
    },
    clearBusinessCon() {
      this.businessCon = {
        name: '',
        model: '',
        parameter: '',
        brand: '',
        producers: '',
        producingArea: '',
        number: '',
        unit: '',
        unitPrice: '',
        sumPrice: ''
      }
      this.$refs.businessCon.resetFields();
    },
    handleEntrustSave() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '修改数据中')
          ])
        }
      });
      that.commonUtil.ajaxPutUtil('/contract/updateProduct', that.editForm, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.$Message.success(res.msg);
          that.refreshList()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    formatData(date) {
      return this.$moment(date).format('YYYY-MM-DD')
    },
    // 获取商品内容列表
    refreshList(callback) {
      const that = this
      that.editIndex = -1;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/contract/searchContractInfo/' + that.form.contractId, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          const detailList = data.productListCollection
          that.entrustList = detailList.productCollections
          that.sumMoney = detailList.sumMoney
          if (callback) {
            callback(data)
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    handleEntrustDelete(id) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/contract/deleteProduct/' + id, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.refreshList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    handleEntrustEdit(row, index) {
      this.editIndex = index;
      this.editForm = Object.assign({}, row)
    },
    handleInlineCancel() {
      this.businessModel = false;
    },
    handleInlineConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show();
          that.businessCon.contractId = that.form.contractId
          that.commonUtil.ajaxPostUtil('/contract/addProduct', that.businessCon, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.$refs.businessCon.resetFields();
              that.businessModel = false
              that.refreshList();
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    showModal(type) {
      const that = this
      that.clearFormCon()
      if (type) {
        that.form.contractId = that.contractId
        that.form.contractType = type
        if (that.contractId) {
          that.refreshList((data) => {
            // 编辑获取数据，以及文件获取信息
            data.executeTime = that.formatData(data.executeTime)
            data.signDate = that.formatData(data.signDate)
            that.form = data
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems()
            })
          });
        }
      }
      that.setUpModel = true;
    },
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          const isCreate = that.isCreate
          const method = isCreate ? '/contract/addContract' : '/contract/updateContract'
          const form = that.getNewForm()
          if (that.isAdd) {
            that.setUpModel = false;
            that.$emit('refreshList')
          } else {
            that.commonUtil.ajaxCommonUtil(isCreate, method, form, (res) => {
            // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.$Message.success(res.msg);
                that.setUpModel = false;
                that.$emit('refreshList')
              } else {
                that.$Message.error(res.msg);
              }
            }, () => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    clearFormCon() {
      this.form = {
        contractId: null,
        contractType: 2,
        contractName: '',
        budgetGist: '',
        signGist: '',
        contractAttr: '',
        signDate: '',
        executeTime: '',
        yifangName: '',
        yifangAddr: '',
        yifangPhone: '',
        legalPersion: '',
        certigier: '',
        depositBank: '',
        depositBankNum: '',
        dutyParagraph: ''
      };
      this.businessCon = {
        name: '',
        model: '',
        parameter: '',
        brand: '',
        producers: '',
        producingArea: '',
        number: '',
        unit: '',
        unitPrice: '',
        sumPrice: ''
      };
      this.isAdd = false
      this.entrustList = []
      this.sumMoney = null
      this.tabName = 'name1'
      this.$refs.form.resetFields();
      this.$refs.businessCon.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>




