<template>
  <div>
    <Modal
      v-model="isPopUp"
      :mask-closable="false"
      width="75%"
      @on-visible-change="closeModal"
    >
      <p slot="header">
        <span>投资汇总信息</span>
      </p>
      <!--位置 弹出框-->
      <Tabs :animated="false" class="innerTabStyle" @on-click="switchTab">
        <TabPane label="投资汇总表" name="name0">
          <Table key="table1" :columns="totalColumn" :data="totalList" />
          <!-- <div v-if="totalVolume>0" style="text-align:right;margin-top:15px;font-size:20px">
            总投资额：<span style="font-size:24px;color:#2db7f5"><Icon type="logo-yen" />{{ totalVolume }}</span>
          </div> -->
          <bottomTotal name="总投资额" :sum="totalVolume" />
        </TabPane>
        <TabPane label="建筑安装工程" name="name1">
          <Table key="table2" :columns="constructionColumn" :data="constructionList" />
        </TabPane>
        <TabPane label="仪器设备购置" name="name2">
          <Table key="table3" :columns="equipColumn" :data="equipList" />
        </TabPane>
        <TabPane label="其他配套工程" name="name3">
          <Table key="table4" :columns="otherProjectColumn" :data="otherProjectList" />
        </TabPane>
        <TabPane label="工程建设其他费用" name="name4">
          <Table key="table5" :columns="otherCostColumn" :data="otherCostList" />
        </TabPane>
        <TabPane label="预备费" name="name5">
          <Table key="table6" :columns="reserveColumn" :data="reserveList" />
        </TabPane>
        <TabPane class="uploadStyle" label="附件" name="name7">
          <uploadFile ref="uploadFile1" is-disabled :form="form" />
        </TabPane>
      </Tabs>
      <p slot="footer">
        <Button icon="md-print" style="float: left">
          打印
        </Button>
        <Button icon="md-exit" style="float: left">
          导出
        </Button>
        <Button type="primary" @click="isPopUp = false">
          确认
        </Button>
        <Button @click="isPopUp = false">
          取消
        </Button>
      </p>
    </Modal>
    <!--其他经费支出-->
    <Modal
      v-model="otherModal"
      :mask-closable="false"
      width="75%"
      @on-visible-change="closeModal"
    >
      <p slot="header">
        <span>投资汇总信息</span>
      </p>
      <Tabs :animated="false" class="innerTabStyle">
        <TabPane label="其他经费支出明细" name="name6">
          <Table key="table7" :columns="otherColumn" :data="otherList"></Table>
        </TabPane>
        <TabPane class="uploadStyle" label="附件" name="name7">
          <uploadFile ref="uploadFile2" is-disabled :form="form" />
        </TabPane>
      </Tabs>
      <p slot="footer">
        <Button style="float: left" icon="md-print">
          打印
        </Button>
        <Button style="float: left" icon="md-exit">
          导出
        </Button>
        <Button type="primary" @click="otherModal = false">
          确认
        </Button>
        <Button @click="otherModal = false">
          取消
        </Button>
      </p>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import bottomTotal from '@/components/bottomTotal.vue'
import calcu from '@/common/calculation.js'
export default {
  name: 'ImplementPlan',
  components: {
    uploadFile,
    bottomTotal
  },
  props: {
    searchData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      form: {
        fileids: null
      },
      isPopUp: false,
      otherModal: false,
      projectUnit: {
        1: {
          label: '平方米'
        },
        2: {
          label: '立方米'
        },
        3: {
          label: '米'
        },
        4: {
          label: '个'
        }
      },
      equipUnit: {
        1: {
          label: '个'
        },
        2: {
          label: '座'
        },
        3: {
          label: '台（套）'
        }
      },
      // 建筑安装工程
      constructionColumn: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '分项名称',
          key: 'auditSubentryName'
        },
        {
          title: '建设内容',
          key: 'auditSubentryContent'
        },
        {
          title: '单位',
          key: 'auditSubentryUnit',
          render: (h, params) => {
            const row = params.row
            const label = this.getUnitName(row.auditSubentryUnit, row.auditSubentryType)
            return h('div', label)
          }
        },
        {
          title: '规模',
          key: 'auditSubentryScale'
        },
        {
          title: '单价（元）',
          key: 'auditSubentryPrice'
        },
        {
          title: '投资额（万元）',
          key: 'auditSubentryVolume'
        }
      ],
      constructionList: [],
      // 仪器设备购置
      equipColumn: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '分项名称',
          key: 'auditSubentryName'
        },
        {
          title: '设备名称',
          key: 'auditSubentryContent'
        },
        {
          title: '型号',
          key: 'equipType'
        },
        {
          title: '参数',
          key: 'equipParam'
        },
        {
          title: '单位',
          key: 'auditSubentryUnit',
          render: (h, params) => {
            const row = params.row
            const label = this.getUnitName(row.auditSubentryUnit, row.auditSubentryType)
            return h('div', label)
          }
        },
        {
          title: '规模',
          key: 'auditSubentryScale'
        },
        {
          title: '单价',
          key: 'auditSubentryPrice'
        },
        {
          title: '投资额（万元）',
          key: 'auditSubentryVolume'
        }
      ],
      equipList: [
      ],
      // 仪器设备配置
      otherProjectColumn: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '分项名称',
          key: 'auditSubentryName'
        },
        {
          title: '建设内容',
          key: 'auditSubentryContent'
        },
        {
          title: '单位',
          key: 'auditSubentryUnit',
          render: (h, params) => {
            const row = params.row
            const label = this.getUnitName(row.auditSubentryUnit, row.auditSubentryType)
            return h('div', label)
          }
        },
        {
          title: '规模',
          key: 'auditSubentryScale'
        },
        {
          title: '单价',
          key: 'auditSubentryPrice'
        },
        {
          title: '投资额（万元）',
          key: 'auditSubentryVolume'
        }
      ],
      otherProjectList: [],
      // 工程建设其他费用
      otherCostColumn: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '分项名称',
          key: 'auditSubentryName'
        },
        {
          title: '费用名称',
          key: 'auditSubentryContent'
        },
        {
          title: '计取比例',
          key: 'costScale'
        },
        {
          title: '计取基数',
          key: 'costCardinal'
        },
        {
          title: '投资额（万元）',
          key: 'auditSubentryVolume'
        }
      ],
      otherCostList: [],
      // 预备费表格
      reserveColumn: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '分项名称',
          key: 'auditSubentryName'
        },
        {
          title: '费用名称',
          key: 'auditSubentryContent'
        },
        {
          title: '计取比例',
          key: 'costScale'
        },
        {
          title: '计取基数',
          key: 'costCardinal'
        },
        {
          title: '投资额（万元）',
          key: 'auditSubentryVolume'
        }
      ],
      reserveList: [
      ],
      // 其他配套工程
      otherColumn: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '分项名称',
          key: 'auditSubentryName'
        },
        {
          title: '建设内容',
          key: 'auditSubentryContent'
        },
        {
          title: '单位',
          key: 'auditSubentryUnit'
        },
        {
          title: '规模',
          key: 'auditSubentryScale'
        },
        {
          title: '单价',
          key: 'auditSubentryPrice',
          render: (h, params) => {
            const row = params.row
            const label = this.getUnitName(row.auditSubentryUnit, row.auditSubentryType)
            return h('div', label)
          }
        },
        {
          title: '投资额（万元）',
          key: 'auditSubentryVolume'
        }
      ],
      otherList: [
      ],
      // 投资汇总表
      totalColumn: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '分项名称',
          key: 'auditSubentryName'
        },
        {
          title: '内容',
          key: 'auditSubentryContent'
        },
        {
          title: '单位',
          key: 'auditSubentryUnit',
          render: (h, params) => {
            const row = params.row
            const label = this.getUnitName(row.auditSubentryUnit, row.auditSubentryType)
            return h('div', label)
          }
        },
        {
          title: '规模',
          key: 'auditSubentryScale'
        },
        {
          title: '投资额（万元）',
          key: 'auditSubentryVolume',
          align: 'right'
        }
      ],
      totalList: [
      ],
      tabIndex: 0
    }
  },
  computed: {
    totalVolume() {
      let total = 0
      this.totalList.forEach(item => {
        total = calcu.accAdd(total, item.auditSubentryVolume)
      })
      return total;
    }
  },
  methods: {
    search() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/auditSubentry/searchAllAuditSubentry', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        const data = res.data
        if (res.code === 1) {
          for (const key in data) {
            const item = data[key]
            if (parseInt(key) === 7) {
              // 文件附件
              that.form = {
                fileids: item
              }
            } else if (item.length !== 0) {
              that.getRightType(key, item)
            }
          }
        } else {
          that.$Message.error(res.msg);
        }
        if (data[6].length !== 0) {
          that.$nextTick(() => {
            that.$refs.uploadFile2.getFileItems()
            that.otherModal = true
          })
        } else {
          that.$nextTick(() => {
            that.$refs.uploadFile1.getFileItems()
            that.isPopUp = true
          })
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    getUnitName(unit, auditSubentryType) {
      const tabIndex = this.tabIndex
      if (unit === null) return ''
      const unitArray = this.getUnitArray(tabIndex === 0 ? auditSubentryType : tabIndex)
      return unit ? unitArray[unit].label : ''
    },
    // 切换tab
    switchTab(name) {
      const nameArray = ['name0', 'name1', 'name2', 'name3', 'name4', 'name5', 'name6']
      const index = nameArray.indexOf(name)
      this.tabIndex = index
    },
    getUnitArray(index) {
      return index === 2 ? this.equipUnit : this.projectUnit
    },
    closeModal(flag) {
      if (!flag) {
        this.constructionList = []
        this.equipList = []
        this.otherProjectList = []
        this.otherCostList = []
        this.reserveList = []
        this.otherList = []
        this.totalList = []
        this.form = {
          fileids: null
        }
      }
    },
    getRightType(index, data) {
      switch (parseInt(index)) {
        case 0:
          this.totalList = data
          break;
        case 1:
          this.constructionList = data
          break;
        case 2:
          this.equipList = data
          break;
        case 3:
          this.otherProjectList = data
          break;
        case 4:
          this.otherCostList = data
          break;
        case 5:
          this.reserveList = data
          break;
        case 6:
          this.otherList = data
          break;
      }
    }
  }
}
</script>



