<template>
  <div class="income">
    <Modal
      v-model="constructionModel"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled">请添加建设工程施工合同</span>
        <span v-else>建设工程施工合同</span>
      </p>
      <Form
        ref="form"
        :model="form"
        :rules="formType"
        :label-width="80"
      >
        <Tabs
          v-model="tabName"
          :animated="false"
          class="innerTabStyle"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >

            <Row>
              <Col span="12">
              <FormItem
                label="合同名称"
                prop="contractName"
              >
                <Input
                  v-model.trim="form.contractName"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写合同名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="预算依据"
                prop="budgetGist"
              >
                <Select
                  v-model="form.budgetGist"
                  :disabled="isDisabled"
                  :transfer="true"
                  filterable
                  placeholder="请选择预算依据"
                >
                  <Option v-for="(item,key) in budgetGist" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="签订依据"
                prop="signGist"
              >
                <Select
                  v-model="form.signGist"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请填写签订依据"
                >
                  <Option v-for="(item,key) in signGist" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="合同属性"
                prop="contractAttr"
              >
                <Select
                  v-model="form.contractAttr"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请填写合同属性"
                >
                  <Option v-for="(item,key) in contractAttr" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="签订日期"
                prop="signDate"
              >
                <DatePicker v-model="form.signDate" :disabled="isDisabled" type="date" placeholder="请选择签订日期" style="width: 100%" transfer clearable></DatePicker>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="执行时间"
                prop="executeTime"
              >
                <DatePicker v-model="form.executeTime" :disabled="isDisabled" type="date" placeholder="请选择执行时间" style="width: 100%" transfer clearable></DatePicker>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="工程名称"
                prop="engineName"
              >
                <Input
                  v-model.trim="form.engineName"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写工程名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="工程地点"
                prop="engineAddr"
              >
                <Input
                  v-model.trim="form.engineAddr"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写工程地点"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="承包范围"
                prop="jobScope"
              >
                <Input
                  v-model.trim="form.jobScope"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写承包范围"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="承包方式"
                prop="jobWay"
              >
                <Input
                  v-model.trim="form.jobWay"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写承包方式"
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            label="工程内容"
            name="name2"
          >
            <!-- table-content -->
            <Content
              class="content"
              :style="{padding: '14px 24px',background: '#fff'}"
            >
              <Button
                v-if="!isDisabled"
                type="primary"
                icon="md-add"
                @click="addContructInfo()"
              >
                添加
              </Button>
              <Table
                highlight-row
                class="table"
                :columns="entrustColumns"
                :data="constructionList"
                style="margin-top: 15px;"
              >
                <template slot="engineInfo" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.engineInfo" type="text" />
                  <span v-else>{{ row.engineInfo }}</span>
                </template>
                <template slot="structure" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.structure" type="text" />
                  <span v-else>{{ row.structure }}</span>
                </template>
                <template slot="scale" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.scale" type="text" />
                  <span v-else>{{ row.scale }}</span>
                </template>
                <template slot="sumMoney" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.sumMoney" type="text" />
                  <span v-else>{{ row.sumMoney }}</span>
                </template>
                <template v-if="!isDisabled" slot="action" slot-scope="{ row, index }">
                  <div v-if="editIndex === index">
                    <Button type="text" @click="handleEntrustSave()">
                      保存
                    </Button>
                    <Button type="text" @click="handleConstructionDelete(row.id)">
                      删除
                    </Button>
                    <Button type="text" @click="editIndex = -1">
                      取消
                    </Button>
                  </div>
                  <div v-else>
                    <Button type="text" @click="handleConstructionEdit(row, index)">
                      操作
                    </Button>
                  </div>
                </template>
              </Table>
              <bottomTotal name="工程总造价" :sum="sumMoney" />
            </Content>
          </TabPane>
          <TabPane
            label="乙方信息"
            name="name3"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="乙方名称"
                prop="yifangName"
              >
                <Input
                  v-model.trim="form.yifangName"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写乙方名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="乙方地址"
                prop="yifangAddr"
              >
                <Input
                  v-model.trim="form.yifangAddr"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写乙方地址"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="联系电话"
                prop="yifangPhone"
              >
                <Input
                  v-model.trim="form.yifangPhone"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写联系电话"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="法人代表"
                prop="legalPersion"
              >
                <Input
                  v-model.trim="form.legalPersion"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写法人代表"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="授权人"
                prop="certigier"
              >
                <Input
                  v-model.trim="form.certigier"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写授权人"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="开户银行"
                prop="depositBank"
              >
                <Input
                  v-model.trim="form.depositBank"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写开户银行"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="账号"
                prop="depositBankNum"
              >
                <Input
                  v-model.trim="form.depositBankNum"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写账号"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="税号"
                prop="dutyParagraph"
              >
                <Input
                  v-model.trim="form.dutyParagraph"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写税号"
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            label="附件"
            name="name4"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="{fileids: form.contractFileIds}" />
          </TabPane>
        </Tabs>
      </Form>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          确认
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="constructionModel = false"
        >
          取消
        </Button>
      </p>

    </Modal>

    <Modal
      v-model="businessModel"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">请添加工程内容</p>
      <Form
        ref="businessCon"
        :model="businessCon"
        :rules="businessConRules"
        :label-width="80"
      >
        <Row>
          <Col span="12">
          <FormItem
            label="工程内容"
            prop="engineInfo"
          >
            <Input
              v-model.trim="businessCon.engineInfo"
              :disabled="isDisabled"
              clearable
              placeholder="请填写工程内容"
            ></Input>
          </FormItem>
          </Col>
          <Col span="12">
          <FormItem
            label="结构"
            prop="structure"
          >
            <Input
              v-model.trim="businessCon.structure"
              :disabled="isDisabled"
              clearable
              placeholder="请填写结构"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12">
          <FormItem
            label="规模"
            prop="scale"
          >
            <Input
              v-model.trim="businessCon.scale"
              :disabled="isDisabled"
              clearable
              placeholder="请填写规模"
            ></Input>
          </FormItem>
          </Col>
          <Col span="12">
          <FormItem
            label="工程造价"
            prop="sumMoney"
          >
            <Input
              v-model.trim="businessCon.sumMoney"
              :disabled="isDisabled"
              clearable
              placeholder="请填写工程造价"
            ></Input>
          </FormItem>
          </Col>
        </Row>
      </Form>
      <p
        slot="footer"
        style="text-align: left; height:30px"
      >
        <Button
          style="float: right;margin-left: 8px"
          @click="handleInlineCancel"
        >
          取消
        </Button>
        <Button
          style="float: right"
          type="primary"
          @click="handleInlineConfirm"
        >
          确认
        </Button>
      </p>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import bottomTotal from '@/components/bottomTotal.vue'
export default {
  name: 'ConstructionModel',
  components: { uploadFile, bottomTotal },
  props: {
    isDisabled: {
      type: Boolean,
      value: false
    },
    isCreate: {
      type: Boolean,
      value: false
    },
    signGist: {
      type: Object,
      default: () => {}
    },
    budgetGist: {
      type: Object,
      default: () => {}
    },
    contractAttr: {
      type: Object,
      default: () => {}
    },
    contractId: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      isAdd: false,
      tabName: 'name1',
      sumMoney: null,
      constructionModel: false,
      businessModel: false,
      editIndex: -1, // 当前聚焦的输入框的行数
      // 点击操作之后的文本框的转变
      editForm: {
        engineInfo: '',
        structure: '',
        scale: '',
        sumMoney: ''
      },
      constructionList: [],
      form: {
        contractId: null,
        contractType: 1,
        contractName: '',
        budgetGist: '',
        signGist: '',
        contractAttr: '',
        signDate: '',
        executeTime: '',
        yifangName: '',
        yifangAddr: '',
        yifangPhone: '',
        legalPersion: '',
        certigier: '',
        depositBank: '',
        depositBankNum: '',
        dutyParagraph: '',
        engineName: '',
        engineAddr: '',
        jobScope: '',
        jobWay: ''
      },
      businessCon: {
        engineInfo: '',
        structure: '',
        scale: '',
        sumMoney: ''
      },
      businessConRules: {
        engineInfo: [
          { required: true, message: '请填写工程内容', trigger: 'blur' }
        ],
        structure: [
          { required: true, message: '请填写结构', trigger: 'blur' }
        ],
        scale: [
          { required: true, message: '请填写规模', trigger: 'blur' }
        ],
        sumMoney: [
          { required: true, message: '请填写工程造价', trigger: 'blur' }
        ]
      },
      formType: {
        engineName: [
          { required: true, message: '请填写工程名称', trigger: 'blur' }
        ],
        engineAddr: [
          { required: true, message: '请填写工程地点', trigger: 'blur' }
        ],
        jobScope: [
          { required: true, message: '请填写承包范围', trigger: 'blur' }
        ],
        jobWay: [
          { required: true, message: '请填写承包方式', trigger: 'blur' }
        ],
        contractName: [
          { required: true, message: '请填写合同名称', trigger: 'blur' }
        ],
        budgetGist: [
          { type: 'number', required: true, message: '请选择预算依据', trigger: 'change' }
        ],
        signGist: [
          { type: 'number', required: true, message: '请填写签订依据', trigger: 'change' }
        ],
        contractAttr: [
          { type: 'number', required: true, message: '请填写合同属性', trigger: 'change' }
        ],
        signDate: [
          { type: 'date', required: true, message: '请选择签订日期', trigger: 'change' }
        ],
        executeTime: [
          { type: 'date', required: true, message: '请选择执行时间', trigger: 'change' }
        ],
        yifangName: [
          { required: true, message: '请填写乙方名称', trigger: 'blur' }
        ],
        yifangAddr: [
          { required: true, message: '请填写乙方地址', trigger: 'blur' }
        ],
        yifangPhone: [
          { required: true, message: '请填写联系电话', trigger: 'blur' }
        ],
        legalPersion: [
          { required: true, message: '请填写法人代表', trigger: 'blur' }
        ],
        certigier: [
          { required: true, message: '请填写授权人', trigger: 'blur' }
        ],
        depositBank: [
          { required: true, message: '请填写开户银行', trigger: 'blur' }
        ],
        depositBankNum: [
          { required: true, message: '请填写账号', trigger: 'blur' }
        ],
        dutyParagraph: [
          { required: true, message: '请填写税号', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    entrustColumns() {
      return [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '工程内容',
          slot: 'engineInfo'
        },
        {
          title: '结构',
          slot: 'structure'
        },
        {
          title: '规模',
          slot: 'scale'
        },
        {
          title: '工程造价',
          slot: 'sumMoney'
        },
        {
          align: 'center',
          title: '操作',
          slot: 'action',
          width: '130'
        }
      ]
    }
  },
  mounted() {
    this.clearFormCon();
  },
  methods: {
    getNewForm() {
      const form = Object.assign({}, this.form)
      form.signDate = new Date(form.signDate).getTime()
      form.executeTime = new Date(form.executeTime).getTime()
      form.fileIds = this.$refs.uploadFile.getFileIds()
      return form
    },
    addContructInfo() {
      // 需要做判断
      const that = this;
      // 填写好建设工程施工合同的基本信息
      that.$refs.form.validate((valid) => {
        if (valid) {
          if (that.form.contractId) {
            that.clearBusinessCon();
            that.businessModel = true;
          } else {
            // 调用接口，添加
            const form = that.getNewForm()
            that.$Spin.show();
            that.commonUtil.ajaxPostUtil('/contract/addContract', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.isAdd = true
                that.form.contractId = res.data
                that.clearBusinessCon();
                that.businessModel = true;
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请先填写建设工程施工合同的基本信息！');
        }
      })
    },
    clearBusinessCon() {
      this.businessCon = {
        engineInfo: '',
        structure: '',
        scale: '',
        sumMoney: ''
      }
      this.$refs.businessCon.resetFields();
    },
    handleEntrustSave() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '修改数据中')
          ])
        }
      });
      that.commonUtil.ajaxPutUtil('/engineDetail/updateEngineDetail', that.editForm, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.$Message.success(res.msg);
          that.refreshList()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    handleConstructionDelete(id) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/engineDetail/deleteEngineDetail/' + id, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.refreshList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    handleConstructionEdit(row, index) {
      this.editIndex = index;
      this.editForm = Object.assign({}, row)
    },
    handleInlineCancel() {
      this.businessModel = false;
      this.$refs.businessCon.resetFields();
    },
    // 工程内容的确定
    handleInlineConfirm() {
      const that = this;
      that.$refs.businessCon.validate((valid) => {
        if (valid) {
          that.$Spin.show();
          that.businessCon.contractId = that.form.contractId
          that.commonUtil.ajaxPostUtil('/engineDetail/addEngineDetail', that.businessCon, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.$refs.businessCon.resetFields();
              that.businessModel = false
              that.refreshList();
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    formatData(date) {
      return this.$moment(date).format('YYYY-MM-DD')
    },
    // 获取工程内容列表
    refreshList(callback) {
      const that = this
      that.editIndex = -1;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/contract/searchContractInfo/' + that.form.contractId, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          const detailList = data.engineDeatilListColletcion
          that.constructionList = detailList.list
          that.sumMoney = detailList.sumMoney
          if (callback) {
            callback(data)
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    showModal(type) {
      const that = this
      that.clearFormCon()
      if (type) {
        that.form.contractId = that.contractId
        that.form.contractType = type
        if (that.contractId) {
          that.refreshList((data) => {
            // 编辑获取数据，以及文件获取信息
            data.executeTime = that.formatData(data.executeTime)
            data.signDate = that.formatData(data.signDate)
            that.form = data
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems()
            })
          });
        }
      }
      that.constructionModel = true;
    },
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          const isCreate = that.isCreate
          const method = isCreate ? '/contract/addContract' : '/contract/updateContract'
          const form = that.getNewForm()
          if (that.isAdd) {
            that.constructionModel = false;
            that.$emit('refreshList')
          } else {
            that.$Spin.show();
            that.commonUtil.ajaxCommonUtil(isCreate, method, form, (res) => {
            // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.$Message.success(res.msg);
                that.constructionModel = false;
                that.$emit('refreshList')
              } else {
                that.$Message.error(res.msg);
              }
            }, () => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    clearFormCon() {
      this.form = {
        contractId: null,
        contractType: 1,
        contractName: '',
        budgetGist: '',
        signGist: '',
        contractAttr: '',
        signDate: '',
        executeTime: '',
        yifangName: '',
        yifangAddr: '',
        yifangPhone: '',
        legalPersion: '',
        certigier: '',
        depositBank: '',
        depositBankNum: '',
        dutyParagraph: ''
      };
      this.businessCon = {
        engineInfo: '',
        structure: '',
        scale: '',
        sumMoney: ''
      }
      this.sumMoney = null
      this.isAdd = false
      this.tabName = 'name1'
      this.constructionList = []
      this.$refs.form.resetFields();
      this.$refs.businessCon.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>




