<template>
  <!--详情弹出框-->
  <Modal
    v-model="checkModel"
    class="selStyle specialWidth"
    :mask-closable="false"
  >
    <p slot="header">
      <span>审核信息</span>
    </p>
    <Tabs
      :animated="false"
      class="innerTabStyle"
      value="name1"
    >
      <TabPane
        label="信息设置"
        name="name1"
      >
        <Form
          ref="form"
          :model="form"
          :label-width="80"
        >
          <Col span="24">
          <FormItem label="审核结果" :style="{'color':form.result.color}">
            {{ form.result.name }}
          </FormItem>
          </Col>
          <Col span="24">
          <FormItem label="审核人">
            {{ form.auditMan }}
          </FormItem>
          </Col>
          <Col span="24">
          <FormItem label="审核意见">
            {{ form.auditMessage }}
          </FormItem>
          </Col>
          <Col span="24">
          <FormItem label="审核时间">
            {{ form.auditTime }}
          </FormItem>
          </Col>
        </Form>
      </TabPane>
      <TabPane
        class="uploadStyle"
        label="附件"
        name="name7"
      >
        <uploadFile ref="uploadFile" is-disabled :form="form" />
      </TabPane>
    </Tabs>
    <p slot="footer">
      <Button
        style="float: left"
        icon="md-print"
      >
        打印
      </Button>
      <Button
        style="float: left"
        icon="md-exit"
      >
        导出
      </Button>
      <Button
        style="margin-left: 8px"
        @click="resetCheck('form')"
      >
        取消
      </Button>
    </p>
  </Modal>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'Audit',
  components: {
    uploadFile
  },
  props: {
    form: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      // 审核详情模态框
      checkModel: false
    }
  },
  mounted() {

  },
  methods: {
    displayAudit() {
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
        this.checkModel = true;
      })
    },
    resetCheck(name) {
      this.$refs[name].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
      this.checkModel = false;
    }
  }
}
</script>



