<template>
  <div class="fileUpload">
    <div v-if="!isDisabled">
      <b>
        <Icon
          size="16"
          type="md-alert"
          style="margin-right:10px;"
        />请上传与设置信息一致的附件或证明性文件，本系统只接受{{ isJpg?'JPG、JPEG、PNG':(isPdf?'PDF':'PDF、WORD') }}格式文件，请上传前进行确认。</b>
      <Upload
        ref="uploadBox"
        :before-upload="handleUpload"
        :accept="isJpg?'.jpg,.jpeg,.png':(isPdf?'.pdf':'.pdf,.doc')"
        :disabled="isDisabled"
        :on-success="uploadSuccess"
        :on-error="onError"
        :show-upload-list="false"
        :headers="uploadHead()"
        :action="uploadUrl"
      >
        <Button
          class="uploadBtn"
          style="border: none;color: #2b85e4"
          icon="ios-cloud-upload-outline"
        >
          点击此处上传
        </Button>
      </Upload>
    </div>
    <ul class="ivu-upload-list">
      <li v-for="(item, key) of fileItems" :key="item.id" class="ivu-upload-list-file ivu-upload-list-file-finish">
        <span @click="onPreview(item.id,item.name,item.url)"><i class="ivu-icon ivu-icon-md-document"></i>{{ item.name }}</span>
        <Icon type="md-download" @click="downloadPdf(item.id,item.name,item.url)" />
        <span v-if="!isDisabled" style="margin-left:10px;" @click="showFileModel(item.id,item.name,key)">修改文件名</span>
        <i v-if="!isDisabled" class="ivu-icon ivu-icon-ios-close ivu-upload-list-remove" @click="onRemove(item.id,key)"></i>
      </li>
    </ul>
    <Modal
      v-model="fileModal"
      title="修改文件名称"
      @on-ok="updateFileName"
    >
      <Form
        ref="form"
        :model="fileForm"
        :rules="fileFormRules"
        :label-width="80"
      >
        <FormItem
          label="文件名称"
          prop="name"
        >
          <Input
            v-model.trim="fileForm.name"
            clearable
            placeholder="请填写文件名称"
          ></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import axiosUtils from '@/common/axiosUtils.js'
export default {
  name: 'FileUpload',
  props: {
    isDisabled: {
      type: Boolean,
      default: false
    },
    isCreate: {
      type: Boolean,
      default: true
    },
    form: {
      type: Object,
      default: () => {}
    },
    isPdf: {
      type: Boolean,
      default: true
    },
    isJpg: {
      type: Boolean,
      default: false
    },
    num: {
      type: Number,
      default: 10
    },
    flag: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      fileForm: {
        index: null,
        id: null,
        name: ''
      },
      fileModal: false,
      uploadUrl: this.flag ? 'http://api.tjjnht.cn/api/uploadFile' : process.env.UPLOAD_API + 'web/file/upload',
      // 文件上传
      fileItems: [],
      fileFormRules: {
        name: [
          { required: true, message: '请选择文件名称', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    updateFileName() {
      const that = this
      that.$refs.form.validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          const searchData = that.fileForm
          that.commonUtil.ajaxPutUtil('/file/update', searchData, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              const index = searchData.index
              const fileItems = that.fileItems
              fileItems[index].name = searchData.name
              that.fileItems = fileItems
              that.$Message.success('修改文件名成功！');
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            that.$Spin.hide();
          }, that)
        }
      })
    },
    showFileModel(id, name, index) {
      this.fileForm = { id, name, index }
      this.fileModal = true
    },
    clearFileItems() {
      this.fileItems = []
    },
    // 编辑时获取文件详情
    getFileItems(list) {
      const that = this;
      if (that.flag) {
        that.fileItems = list.map(item => {
          const url = item.enclosureUrl
          return {
            id: item.id,
            url: url,
            name: url.substring(url.lastIndexOf('\/') + 1, url.length)
          }
        })
        return;
      }
      const fileids = this.form.fileids
      that.clearFileItems()
      if (fileids) {
        that.$Spin.show({
          render: (h) => {
            return h('div', [
              h('Icon', {
                'class': 'demo-spin-icon-load',
                props: {
                  type: 'ios-loading',
                  size: 18
                }
              }),
              h('div', '加载中')
            ])
          }
        });
        that.commonUtil.ajaxGetUtil('/file/searchFileInfoByIds/' + fileids, {}, (res) => {
        // 关闭数据加载
          that.$Spin.hide();
          if (res.code === 1) {
            that.fileItems = res.data
          } else {
            that.$Message.error(res.msg);
          }
        }, (error) => {
          that.$Message.error('网络连接错误');
          that.$Spin.hide();
        }, that)
      }
    },
    // 下载文件
    downloadPdf(id, name, url) {
      const that = this;
      if (id) {
        if (that.flag) {
          const elt = document.createElement('a');
          elt.setAttribute('href', url);
          elt.setAttribute('download', name);
          elt.style.display = 'none';
          document.body.appendChild(elt);
          elt.click();
          document.body.removeChild(elt);
        } else {
          that.$Spin.show();
          that.commonUtil.downFile('/file/download', { fileId: id }, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            that.savePdf(name, res)
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      } else {
        that.$Message.error('下载失败');
      }
    },
    savePdf(name, res) {
      const flag = this.flag
      const blob = new Blob([res]);
      const link = document.createElement('a');
      link.href = flag ? res : window.URL.createObjectURL(blob);
      link.download = name
      // 此写法兼容可火狐浏览器
      document.body.appendChild(link);
      const evt = document.createEvent('MouseEvents');
      evt.initEvent('click', false, false);
      link.dispatchEvent(evt);
      document.body.removeChild(link);// 下载完成移除元素
    },
    // 得到附件的集合（验收管理）
    getFiles() {
      return this.fileItems.map(item => {
        return {
          enclosureUrl: item.url
        }
      })
    },
    // 得到ids
    getFileIds() {
      return this.fileItems.map(item => {
        return item.id
      }).join(',')
    },
    // 预览
    onPreview(id, name, url) {
      if (this.flag) {
        var onlineUrl = 'http://39.107.79.40:8012/onlinePreview?url=' + encodeURIComponent(url);
        window.open(onlineUrl);
        return;
      }
      this.commonUtil.onlinePreview(id, name)
    },
    // header
    uploadHead() {
      if (this.flag) {
        return {}
      }
      return this.commonUtil.getToken()
    },
    // 再次点击上传之前，清空之前已上传文件
    handleClearFiles() {
      this.$refs.uploadBox.clearFiles()
    },
    // 上传前需要的一些操作
    handleUpload(file) {
      this.handleClearFiles()
      var fileType = this.isJpg ? 'jpg|jpeg|png' : (this.isPdf ? 'pdf' : 'pdf|doc|docx');
      const fileName = file.name;
      const fileExt = fileName.substr(fileName.lastIndexOf('.') + 1).toLowerCase();
      const isLt2M = file.size / 1024 / 1024 < 50;
      const isOk = !(fileType.indexOf(fileExt) === -1);
      const fileLen = this.fileItems.length < this.num
      if (!isOk) {
        const name = this.isJpg ? 'jpg、jpeg、png' : (this.isPdf ? 'pdf' : 'pdf、doc')
        this.$Message.error('文件格式暂只支持' + name + '!');
      } else if (!isLt2M) {
        this.$Message.error('上传文件大小不能超过 50MB!');
      } else if (!fileLen) {
        this.$Message.error('上传文件数量不能大于' + this.num + '个');
      }
      if (isOk && isLt2M && fileLen) {
        this.$Spin.show({
          render: (h) => {
            return h('div', [
              h('Icon', {
                'class': 'demo-spin-icon-load',
                props: {
                  type: 'ios-loading',
                  size: 18
                }
              }),
              h('div', '正在上传附件中，请稍候......')
            ])
          }
        });
      }
      return isOk && isLt2M && fileLen;
    },
    // 上传成功时调用的方法
    uploadSuccess(response, file, fileList) {
      this.$Spin.hide();
      const flag = this.flag
      const success = flag ? response.ok : response.code === 1
      if (success) {
        const data = flag ? response : response.data
        this.fileItems.push({
          id: flag ? file.uid : data.id,
          name: flag ? file.name : data.name,
          url: flag ? data.data : data.url
        })
        this.$Message.success('上传成功');
      } else {
        this.$Message.error(flag ? response.message : response.msg);
      }
    },
    onError() {
      this.$Spin.hide();
      this.$Message.error('上传失败');
    },
    onRemove(id, index) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          const flag = that.flag;
          if (flag && that.isCreate) {
            that.fileItems.splice(index, 1);
            return;
          }
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          if (flag) {
            axiosUtils.ajaxPostUtil('/deleteAcceptancePicture?pictureId=' + id, {}, (res) => {
              that.$Spin.hide();
              if (res.ok) {
                that.fileItems.splice(index, 1);
              } else {
                that.$Message.error(res.message);
              }
            }, () => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.commonUtil.ajaxDeleteUtil('/file/deleteFile/' + id, (res) => {
              that.$Spin.hide();
              if (res.code === 1) {
                that.fileItems.splice(index, 1);
              } else {
                that.$Message.error(res.msg);
              }
            }, () => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        }
      });
    }
  }
}
</script>
<style scope>
  .fileUpload .ivu-upload-list-file .ivu-icon-md-download{
    margin-left:20px;
    color:red;
    cursor: pointer;
  }
</style>



