<template>
  <div class="mulUploadFile">
    <div v-if="!isDisabled">
      <b>
        <Icon
          size="16"
          type="md-alert"
          style="margin-right:10px;"
        />请上传与设置信息一致的附件或证明性文件，本系统只接受{{ isPdf?'PDF':'PDF、WORD' }}格式文件，请上传前进行确认。</b>
    </div>
    <div v-for="(item,key) in uploadList" :key="key">
      <span class="fileName"><b>{{ key }}</b>{{ '、' +item }}</span>
      <Upload
        :ref="'uploadBox'+key"
        :before-upload="handleUpload"
        :accept="isPdf?'.pdf':'.pdf,.doc'"
        :disabled="isDisabled"
        :on-success="uploadSuccess"
        :on-error="onError"
        :show-upload-list="false"
        :headers="uploadHead()"
        :action="uploadUrl"
        style="display:inline-block;"
      >
        <Button
          class="uploadBtn"
          style="border: none;color: #2b85e4"
          icon="ios-cloud-upload-outline"
          @click="uploadIndex=key"
        >
          点击此处上传
        </Button>
      </Upload>
      <ul v-if="fileItems[key]&&fileItems[key].length>0" class="ivu-upload-list">
        <li v-for="content of fileItems[key]" :key="content.id" class="ivu-upload-list-file ivu-upload-list-file-finish">
          <span @click="onPreview(content.fileId,content.name)"><i class="ivu-icon ivu-icon-md-document"></i>{{ content.fileName }}</span>
          <Icon type="md-download" @click="downloadPdf(content.fileId,content.name)" />
          <i v-if="!isDisabled" class="ivu-icon ivu-icon-ios-close ivu-upload-list-remove" @click="onRemove(key,content.fileId)"></i>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  name: 'MulUploadFile',
  props: {
    isDisabled: {
      type: Boolean,
      default: false
    },
    isPdf: {
      type: Boolean,
      default: true
    },
    uploadList: {
      type: Object,
      default: () => {
        return {}
      }
    },
    num: {
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      uploadIndex: null,
      fileItems: {},
      uploadUrl: process.env.UPLOAD_API + 'api/file/upload'
    }
  },
  mounted() {

  },
  methods: {
    // 编辑获取数据
    getFileItems(relationFileids) {
      if (relationFileids && relationFileids.length > 0) {
        const json = {}
        let content;
        for (const item of relationFileids) {
          content = {
            id: item.id,
            fileId: item.fileId,
            fileName: item.fileName
          }
          if (json[item.fileType]) {
            json[item.fileType].push(content)
          } else {
            json[item.fileType] = [content]
          }
        }
        this.$set(this, 'fileItems', json)
      }
    },
    // 得到ids
    getFileIds() {
      const allFilIds = {}
      const fileItems = this.fileItems
      for (const key in fileItems) {
        if (fileItems[key] && fileItems[key].length > 0) {
          allFilIds[key] = fileItems[key].map(content => {
            return content.fileId
          }).join(',')
        }
      }
      return allFilIds
    },
    // 下载文件
    downloadPdf(id, name) {
      const that = this;
      if (id) {
        that.$Spin.show();
        that.commonUtil.downFile('/file/download', { fileId: id }, (res) => {
        // 关闭数据加载
          that.$Spin.hide();
          that.savePdf(name, res)
        }, (error) => {
          that.$Message.error('网络连接错误');
          // 关闭数据加载
          that.$Spin.hide();
        }, that)
      } else {
        that.$Message.error('下载失败');
      }
    },
    savePdf(name, res) {
      const blob = new Blob([res]);
      const link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      link.download = name
      // 此写法兼容可火狐浏览器
      document.body.appendChild(link);
      const evt = document.createEvent('MouseEvents');
      evt.initEvent('click', false, false);
      link.dispatchEvent(evt);
      document.body.removeChild(link);// 下载完成移除元素
    },
    // 预览
    onPreview(id, name) {
      this.commonUtil.onlinePreview(id, name)
    },
    // header
    uploadHead() {
      return this.commonUtil.getToken()
    },
    clearFileItems() {
      this.fileItems = {}
      for (const key of Object.keys(this.uploadList)) {
        this.handleClearFiles(key)
      }
    },
    // 再次点击上传之前，清空之前已上传文件
    handleClearFiles(index) {
      this.$refs['uploadBox' + index][0].clearFiles()
    },
    // 上传前需要的一些操作
    handleUpload(file) {
      const index = this.uploadIndex
      this.handleClearFiles(index)
      const fileType = this.isPdf ? 'pdf' : 'pdf|doc|docx';
      const fileName = file.name;
      const fileExt = fileName.substr(fileName.lastIndexOf('.') + 1).toLowerCase();
      const isLt2M = file.size / 1024 / 1024 < 50;
      const isOk = !(fileType.indexOf(fileExt) === -1);
      const fileLen = (this.fileItems[index] ? this.fileItems[index].length : 0) < this.num
      if (!isOk) {
        const name = this.isPdf ? 'pdf' : 'pdf、doc'
        this.$Message.error('文件格式暂只支持' + name + '!');
      } else if (!isLt2M) {
        this.$Message.error('上传文件大小不能超过 50MB!');
      } else if (!fileLen) {
        this.$Message.error(this.uploadList[index] + '上传文件数量不能大于' + this.num + '个');
      }
      if (isOk && isLt2M && fileLen) {
        this.$Spin.show({
          render: (h) => {
            return h('div', [
              h('Icon', {
                'class': 'demo-spin-icon-load',
                props: {
                  type: 'ios-loading',
                  size: 18
                }
              }),
              h('div', '正在上传附件中，请稍候......')
            ])
          }
        });
      }
      return isOk && isLt2M && fileLen;
    },
    // 上传成功时调用的方法
    uploadSuccess(response, file, fileList) {
      this.$Spin.hide();
      if (response.code === 1) {
        const data = response.data
        const content = {
          fileId: data.id,
          fileName: data.name
        }
        if (this.fileItems[this.uploadIndex]) {
          this.fileItems[this.uploadIndex].push(content)
        } else {
          this.$set(this.fileItems, this.uploadIndex, [content])
        }
        this.$Message.success('上传成功');
      } else {
        this.$Message.error(response.msg);
      }
    },
    onError() {
      this.$Spin.hide();
      this.$Message.error('上传失败');
    },
    onRemove(key, id) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/bidding/deleteFile/' + id, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              const fileItems = that.fileItems[key]
              for (const index in fileItems) {
                if (fileItems[index].fileId === id) {
                  fileItems.splice(index, 1);
                  that.$Message.success('删除成功');
                  break;
                }
              }
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            that.$Spin.hide();
          }, that)
        }
      });
    }
  }
}
</script>
<style scope>
  .mulUploadFile .ivu-upload-list-file .ivu-icon-md-download{
    margin-left:20px;
    color:red;
    cursor: pointer;
  }
  .mulUploadFile .fileName{
    display:inline-block;
    width: 130px;
    margin-left: 25px;
  }
  .mulUploadFile .ivu-upload-list {
    margin: 0px 40px;
  }
</style>



