<template>
  <div class="income">
    <Modal
      v-model="payModel"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled">请添加支出信息</span>
        <span v-else>支出信息</span>
      </p>
      <Tabs
        v-model="tabName"
        :animated="false"
        class="innerTabStyle"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :rules="ruleValidateType"
            :label-width="80"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="类型"
                prop="special_account_type"
              >
                <Select
                  v-model="form.special_account_type"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请选择类型"
                >
                  <Option v-for="(item,key) in contractType" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="日期"
                prop="date"
              >
                <DatePicker v-model="form.date" :disabled="isDisabled" type="date" placeholder="请填写日期" style="width:100%;" transfer clearable></DatePicker>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="凭证号"
                prop="pingzhen"
              >
                <Input
                  v-model.trim="form.pingzhen"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写凭证号"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="借或贷"
                prop="jie_or_dai"
              >
                <Select
                  v-model="form.jie_or_dai"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请选择借或贷"
                >
                  <Option v-for="(item,key) in jieOrDai" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>

              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="借(收入)方金额"
                prop="jiefang_money"
              >
                <Input
                  v-model.trim="form.jiefang_money"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写借(收入)方金额"
                  number
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="对方科目"
                prop="duifang_kemu"
              >
                <Select
                  v-model="form.duifang_kemu"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请选择对方科目"
                >
                  <Option v-for="(item,key) in keMuArray" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="贷(收入)方金额"
                prop="daifang_money"
              >
                <Input
                  v-model.trim="form.daifang_money"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写贷(收入)方金额"
                  number
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="余额"
                prop="balance"
              >
                <Input
                  v-model.trim="form.balance"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写余额"
                  number
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">

              <FormItem
                label="合同名称"
                prop="contract_id"
              >
                <Select
                  v-model="form.contract_id"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请填写合同名称"
                >
                  <Option v-for="item in contractList" :key="item.contractId" :value="parseInt(item.contractId)">{{ item.contractName }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="往来单位"
                prop="wanglai_danwei"
              >
                <Select
                  v-model="form.wanglai_danwei"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请选择往来单位"
                >
                  <Option v-for="item in yiFangList" :key="item.id" :value="parseInt(item.id)">{{ item.name }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="摘要"
                prop="zhaiyao"
              >
                <Input
                  v-model.trim="form.zhaiyao"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写摘要"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Col span="24">
            <FormItem label="备注" prop="remark">
              <Input
                v-model.trim="form.remark"
                :disabled="isDisabled"
                type="textarea"
                :autosize="{minRows: 1,maxRows: 5}"
                placeholder="请填写备注"
              ></Input>
            </FormItem>
            </Col>
          </Form>
        </TabPane>
        <TabPane
          label="附件"
          name="name7"
        >
          <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="{fileids: form.accountFileIds}" />
        </TabPane>
      </Tabs>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="payModel = false"
        >
          取消
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          确认
        </Button>
      </p>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'PayModel',
  components: { uploadFile },
  props: {
    isDisabled: {
      type: Boolean,
      default: false
    },
    isCreate: {
      type: Boolean,
      default: false
    },
    jieOrDai: {
      type: Object,
      default: () => {}
    },
    contractList: {
      type: Array,
      default: () => []
    },
    yiFangList: {
      type: Array,
      default: () => []
    },
    contractType: {
      type: Object,
      default: () => {}
    },
    keMuArray: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      tabName: 'name1',
      payModel: false,
      form: {
        special_account_type: '',
        shouzhi_type: 2,
        date: '',
        pingzhen: '',
        zhaiyao: '',
        duifang_kemu: '',
        jiefang_money: '',
        daifang_money: '',
        jie_or_dai: '',
        balance: '',
        contract_id: '',
        wanglai_danwei: '',
        remark: ''
      },
      ruleValidateType: {
        special_account_type: [
          { type: 'number', required: true, message: '请选择类型', trigger: 'change' }
        ],
        date: [
          { type: 'date', required: true, message: '请填写日期', trigger: 'blur' }
        ],
        pingzhen: [
          { required: true, message: '请填写凭证号', trigger: 'blur' }
        ],
        zhaiyao: [
          { required: true, message: '请填写摘要', trigger: 'blur' }
        ],
        duifang_kemu: [
          { type: 'number', required: true, message: '请选择对方科目', trigger: 'change' }
        ],
        jiefang_money: [
          { type: 'number', required: true, message: '请填写借(收入)方金额', trigger: 'blur' }
        ],
        daifang_money: [
          { type: 'number', required: true, message: '请填写贷(收入)方金额', trigger: 'blur' }
        ],
        jie_or_dai: [
          { type: 'number', required: true, message: '请选择借或贷', trigger: 'change' }
        ],
        balance: [
          { type: 'number', required: true, message: '请填写余额', trigger: 'blur' }
        ],
        contract_id: [
          { type: 'number', required: true, message: '请填写合同名称', trigger: 'blur' }
        ],
        wanglai_danwei: [
          { type: 'number', required: true, message: '请选择往来单位', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          const isCreate = this.isCreate
          const form = Object.assign({}, this.form)
          const method = isCreate ? '/specialAccount/addSpecialAccount' : '/specialAccount/updateSpecialAccount'
          form.date = form.date ? new Date(form.date).getTime() : null
          form.fileIds = this.$refs.uploadFile.getFileIds()
          that.$Spin.show();
          that.commonUtil.ajaxCommonUtil(isCreate, method, form, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.payModel = false;
              that.$Message.success(res.msg);
              that.$emit('refreshList')
            } else {
              that.$Message.error(res.msg);
            }
          }, () => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    showModal(form) {
      this.clearForm()
      if (form) {
        this.form = form
        this.$nextTick(() => {
          this.$refs.uploadFile.getFileItems()
        })
      }
      this.payModel = true;
    },
    clearForm() {
      this.form = {
        special_account_type: '',
        shouzhi_type: 2,
        date: '',
        pingzhen: '',
        zhaiyao: '',
        duifang_kemu: '',
        jiefang_money: '',
        daifang_money: '',
        jie_or_dai: '',
        balance: '',
        contract_id: '',
        wanglai_danwei: '',
        remark: ''
      };
      this.tabName = 'name1'
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>




