<template>
  <div class="income">
    <Modal
      v-model="entrustModel"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled">请添加委托业务合同</span>
        <span v-else>委托业务合同</span>
      </p>
      <Form
        ref="form"
        :model="form"
        :rules="ruleValidateType"
        :label-width="80"
      >
        <Tabs
          v-model="tabName"
          :animated="false"
          class="innerTabStyle"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="合同名称"
                prop="contractName"
              >
                <Input
                  v-model.trim="form.contractName"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写合同名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="预算依据"
                prop="budgetGist"
              >
                <Select
                  v-model="form.budgetGist"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请选择预算依据"
                >
                  <Option v-for="(item,key) in budgetGist" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="签订依据"
                prop="signGist"
              >
                <Select
                  v-model="form.signGist"
                  :disabled="isDisabled"
                  transfer
                  filterable
                  clearable
                  placeholder="请填写签订依据"
                >
                  <Option v-for="(item,key) in signGist" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="合同属性"
                prop="contractAttr"
              >
                <Select
                  v-model="form.contractAttr"
                  :disabled="isDisabled"
                  :transfer="true"
                  filterable
                  placeholder="请填写合同属性"
                >
                  <Option v-for="(item,key) in contractAttr" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="签订日期"
                prop="signDate"
              >
                <DatePicker v-model="form.signDate" :disabled="isDisabled" type="date" placeholder="请选择签订日期" style="width: 100%" :transfer="true"></DatePicker>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="执行时间"
                prop="executeTime"
              >
                <DatePicker v-model="form.executeTime" :disabled="isDisabled" type="date" placeholder="请选择执行时间" style="width: 100%" :transfer="true"></DatePicker>
              </FormItem>
              </Col>
            </Row>
          </tabpane>
          </TabPane>
          <TabPane
            label="委托业务"
            name="name2"
          >
            <!-- table-content -->
            <Content
              class="content"
              :style="{padding: '14px 24px',background: '#fff'}"
            >
              <Button
                type="primary"
                icon="md-add"
                @click="entrustListAdd"
              >
                添加
              </Button>
              <Table
                key="table1"
                ref="entrustList"
                highlight-row
                class="table"
                :columns="entrustColumns"
                :data="entrustList"
                style="margin-top: 15px;"
              >
                <template slot="entrustingContent" slot-scope="{row, index}">
                  <Input v-if="editIndex === index" v-model.trim="editForm.entrustingContent" type="text" />
                  <span v-else>{{ row.entrustingContent }}</span>
                </template>
                <template slot="jobContent" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.jobContent" type="text" />
                  <span v-else>{{ row.jobContent }}</span>
                </template>
                <template slot="summoney" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.summoney" type="text" />
                  <span v-else>{{ row.summoney }}</span>
                </template>
                <template v-if="isbtnShow" slot="action" slot-scope="{ row, index }">
                  <div v-if="editIndex === index">
                    <Button type="text" @click="handleEntrustSave()">
                      保存
                    </Button>
                    <Button type="text" @click="handleEntrustDelete(row.id)">
                      删除
                    </Button>
                    <Button type="text" @click="editIndex = -1">
                      取消
                    </Button>
                  </div>
                  <div v-else>
                    <Button type="text" @click="handleEntrustEdit(row, index)">
                      操作
                    </Button>
                  </div>
                </template>
              </Table>
              <bottomTotal name="总计" :sum="sumMoney" />
            </Content>
          </TabPane>
          <TabPane
            label="乙方信息"
            name="name3"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="乙方名称"
                prop="yifangName"
              >
                <Input
                  v-model.trim="form.yifangName"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写乙方名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="乙方地址"
                prop="yifangAddr"
              >
                <Input
                  v-model.trim="form.yifangAddr"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写乙方地址"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="联系电话"
                prop="yifangPhone"
              >
                <Input
                  v-model.trim="form.yifangPhone"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写联系电话"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="法人代表"
                prop="legalPersion"
              >
                <Input
                  v-model.trim="form.legalPersion"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写法人代表"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="授权人"
                prop="certigier"
              >
                <Input
                  v-model.trim="form.certigier"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写授权人"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="开户银行"
                prop="depositBank"
              >
                <Input
                  v-model.trim="form.depositBank"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写开户银行"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="账号"
                prop="depositBankNum"
              >
                <Input
                  v-model.trim="form.depositBankNum"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写账号"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="税号"
                prop="dutyParagraph"
              >
                <Input
                  v-model.trim="form.dutyParagraph"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写税号"
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            label="附件"
            name="name4"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="{fileids: form.contractFileIds}" />
          </TabPane>
        </Tabs>
      </Form>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          确认
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="entrustModel = false"
        >
          取消
        </Button>
      </p>
    </Modal>

    <Modal
      v-model="businessModel"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled">请添加委托业务合同内容</span>
        <span v-else>委托业务合同内容</span>
      </p>
      <Form
        ref="entrustForm"
        :model="entrustForm"
        :rules="entrustRules"
        :label-width="80"
      >
        <Row>
          <Col span="12">
          <FormItem
            label="委托内容"
            prop="entrustingContent"
          >
            <Input
              v-model.trim="entrustForm.entrustingContent"
              :disabled="isDisabled"
              clearable
              placeholder="请填写委托内容"
            ></Input>
          </FormItem>
          </Col>
          <Col span="12">
          <FormItem
            label="工作内容"
            prop="jobContent"
          >
            <Input
              v-model.trim="entrustForm.jobContent"
              :disabled="isDisabled"
              clearable
              placeholder="请填写工作内容"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12">
          <FormItem
            label="取费基数"
            prop="chargingBase"
          >
            <Input
              v-model.trim="entrustForm.chargingBase"
              :disabled="isDisabled"
              clearable
              placeholder="请填写取费基数"
            ></Input>
          </FormItem>
          </Col>
          <Col span="12">
          <FormItem
            label="比例"
            prop="proportion"
          >
            <Input
              v-model.trim="entrustForm.proportion"
              :disabled="isDisabled"
              clearable
              placeholder="请填写比例"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12">
          <FormItem
            label="总价"
            prop="summoney"
          >
            <Input
              v-model.trim="entrustForm.summoney"
              :disabled="isDisabled"
              clearable
              placeholder="请填写总价"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        </Tabs>
      </Form>
      <p
        slot="footer"
        style="text-align: left; height:30px"
      >
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleInlineConfirm('entrustForm')"
        >
          确认
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="handleInlineCancel('entrustForm')"
        >
          取消
        </Button>
      </p>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import bottomTotal from '@/components/bottomTotal.vue'
export default {
  name: 'EntrustModel',
  components: { uploadFile, bottomTotal },
  props: {
    isDisabled: {
      type: Boolean,
      value: false
    },
    isCreate: {
      type: Boolean,
      value: false
    },
    signGist: {
      type: Object,
      default: () => {}
    },
    budgetGist: {
      type: Object,
      default: () => {}
    },
    contractAttr: {
      type: Object,
      default: () => {}
    },
    contractId: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      isAdd: false,
      tabName: 'name1',
      sumMoney: null,
      entrustModel: false,
      // 当查看详情时，添加 按钮隐藏
      isbtnShow: true,
      businessModel: false,
      editIndex: -1, // 当前聚焦的输入框的行数
      // 点击操作之后的文本框的转变
      editForm: {
        entrustingContent: '',
        jobContent: '',
        summoney: ''
      },
      entrustList: [],
      form: {
        contractId: null,
        contractType: 3,
        contractName: '',
        budgetGist: '',
        signGist: '',
        contractAttr: '',
        signDate: '',
        executeTime: '',
        yifangName: '',
        yifangAddr: '',
        yifangPhone: '',
        legalPersion: '',
        certigier: '',
        depositBank: '',
        depositBankNum: '',
        dutyParagraph: ''
      },
      entrustForm: {
        entrustingContent: '',
        jobContent: '',
        chargingBase: '',
        proportion: '',
        summoney: ''
      },
      entrustRules: {
        entrustingContent: [
          { required: true, message: '请填写委托内容', trigger: 'blur' }
        ],
        jobContent: [
          { required: true, message: '请填写工作内容', trigger: 'blur' }
        ],
        chargingBase: [
          { required: true, message: '请填写取费基数', trigger: 'blur' }
        ],
        proportion: [
          { required: true, message: '请填写比例', trigger: 'blur' }
        ],
        summoney: [
          { required: true, message: '请填写总价', trigger: 'blur' }
        ]
      },
      ruleValidateType: {
        contractName: [
          { required: true, message: '请填写合同名称', trigger: 'blur' }
        ],
        budgetGist: [
          { type: 'number', required: true, message: '请选择预算依据', trigger: 'change' }
        ],
        signGist: [
          { type: 'number', required: true, message: '请填写签订依据', trigger: 'change' }
        ],
        contractAttr: [
          { type: 'number', required: true, message: '请填写合同属性', trigger: 'change' }
        ],
        signDate: [
          { type: 'date', required: true, message: '请选择签订日期', trigger: 'change' }
        ],
        executeTime: [
          { type: 'date', required: true, message: '请选择执行时间', trigger: 'change' }
        ],
        yifangName: [
          { required: true, message: '请填写乙方名称', trigger: 'blur' }
        ],
        yifangAddr: [
          { required: true, message: '请填写乙方地址', trigger: 'blur' }
        ],
        yifangPhone: [
          { required: true, message: '请填写联系电话', trigger: 'blur' }
        ],
        legalPersion: [
          { required: true, message: '请填写法人代表', trigger: 'blur' }
        ],
        certigier: [
          { required: true, message: '请填写授权人', trigger: 'blur' }
        ],
        depositBank: [
          { required: true, message: '请填写开户银行', trigger: 'blur' }
        ],
        depositBankNum: [
          { required: true, message: '请填写账号', trigger: 'blur' }
        ],
        dutyParagraph: [
          { required: true, message: '请填写税号', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    entrustColumns() {
      return [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '委托内容',
          slot: 'entrustingContent'
        },
        {
          title: '工作内容',
          slot: 'jobContent'
        },
        {
          title: '总价',
          slot: 'summoney'
        },
        {
          align: 'center',
          title: '操作',
          slot: 'action',
          width: '130'
        }
      ]
    }
  },
  mounted() {
  },
  methods: {
    getNewForm() {
      const form = Object.assign({}, this.form)
      form.signDate = new Date(form.signDate).getTime()
      form.executeTime = new Date(form.executeTime).getTime()
      form.fileIds = this.$refs.uploadFile.getFileIds()
      return form
    },
    entrustListAdd() {
      // 需要做判断
      const that = this;
      // 填写好设备购置合同的基本信息
      that.$refs.form.validate((valid) => {
        if (valid) {
          if (that.form.contractId) {
            that.businessModel = true;
          } else {
            // 调用接口，添加
            const form = that.getNewForm()
            that.$Spin.show();
            that.commonUtil.ajaxPostUtil('/contract/addContract', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.isAdd = true
                that.form.contractId = res.data
                that.$refs.entrustForm.resetFields();
                that.businessModel = true;
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请先填写设备购置合同的基本信息！');
        }
      })
    },
    formatData(date) {
      return this.$moment(date).format('YYYY-MM-DD')
    },
    // 获取委托业务内容列表
    refreshList(callback) {
      const that = this
      that.editIndex = -1;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/contract/searchContractInfo/' + that.form.contractId, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          const detailList = data.entrustListCollection
          that.entrustList = detailList.searchEntrustCollections
          that.sumMoney = detailList.sumMoney
          if (callback) {
            callback(data)
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    handleEntrustSave() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '修改数据中')
          ])
        }
      });
      that.commonUtil.ajaxPutUtil('/contract/updateEntrust', that.editForm, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.$Message.success(res.msg);
          that.refreshList()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    handleEntrustDelete(id) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/contract/deleteEntrust/' + id, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.refreshList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    handleEntrustEdit(row, index) {
      this.editIndex = index;
      this.editForm = Object.assign({}, row)
    },
    handleInlineCancel() {
      this.businessModel = false;
    },
    handleInlineConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show();
          that.entrustForm.contractId = that.form.contractId
          that.commonUtil.ajaxPostUtil('/contract/addEntrust', that.entrustForm, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.$refs.entrustForm.resetFields();
              that.businessModel = false
              that.refreshList();
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    showModal(type) {
      const that = this
      that.clearFormCon()
      if (type) {
        that.form.contractId = that.contractId
        that.form.contractType = type
        if (that.contractId) {
          that.refreshList((data) => {
            // 编辑获取数据，以及文件获取信息
            data.executeTime = that.formatData(data.executeTime)
            data.signDate = that.formatData(data.signDate)
            that.form = data
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems()
            })
          });
        }
      }
      that.entrustModel = true;
    },
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          const isCreate = that.isCreate
          const method = isCreate ? '/contract/addContract' : '/contract/updateContract'
          const form = that.getNewForm()
          if (that.isAdd) {
            that.entrustModel = false;
            that.$emit('refreshList')
          } else {
            that.commonUtil.ajaxCommonUtil(isCreate, method, form, (res) => {
            // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.$Message.success(res.msg);
                that.entrustModel = false;
                that.$emit('refreshList')
              } else {
                that.$Message.error(res.msg);
              }
            }, () => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请填写好委托业务合同信息');
        }
      })
    },
    clearFormCon() {
      this.form = {
        contractId: null,
        contractType: 3,
        contractName: '',
        budgetGist: '',
        signGist: '',
        contractAttr: '',
        signDate: '',
        executeTime: '',
        yifangName: '',
        yifangAddr: '',
        yifangPhone: '',
        legalPersion: '',
        certigier: '',
        depositBank: '',
        depositBankNum: '',
        dutyParagraph: ''
      };
      this.entrustForm = {
        entrustingContent: '',
        jobContent: '',
        chargingBase: '',
        proportion: '',
        summoney: ''
      };
      this.entrustList = []
      this.tabName = 'name1'
      this.sumMoney = null
      this.isAdd = false
      this.$refs.form.resetFields();
      this.$refs.entrustForm.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    }
  }
}
</script>




