export default {
  data() {
    return {
      isCreate: false,
      total: 0,
      currentRow: {},
      // 设置是否选中行 默认为 false
      isSelected: false,
      // 是否将弹层放置于 body 内  设置为true
      transferValue: true,
      // 控制 是否禁用  默认不禁用
      isDisabled: false,
      status: {
        0: {
          name: '正在审核',
          color: 'blue'
        },
        1: {
          name: '审核通过',
          color: 'green'
        },
        2: {
          name: '审核未通过',
          color: 'red'
        },
        3: {
          name: '未审核',
          color: 'orange'
        }
      },
      // 文件和审核弹窗
      uploadFile: {
        fileids: null
      },
      auditForm: {
        // 审核人
        auditMan: '',
        // 审核结果
        result: { color: 'blue', name: '正在审核' },
        // 审核意见
        auditMessage: '',
        fileids: null
      },
      // 日期搜索
      startTimeOptions: {}, // 开始日期设置
      endTimeOptions: {} // 结束日期设置
    }
  },
  methods: {
    getNewSearchData() {
      const searchData = Object.assign({}, this.searchData)
      const startTime = searchData.startTime
      const endTime = searchData.endTime
      if (startTime) {
        searchData.startTime = new Date(startTime + ' 00:00:00').getTime()
      }
      if (endTime) {
        searchData.endTime = new Date(startTime + ' 23:59:59').getTime()
      }
      return searchData
    },
    search(searchData, callback) {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil(that.listUrl, searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          callback(true, res)
        } else {
          that.$Message.error(res.msg);
          callback(false)
        }
      }, () => {
        callback(false)
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.currentRow = {}
      that.isSelected = false
      that.commonUtil.getAuditCountApi(that)
    },
    // 开始时间和结束时间
    startTimeChange: function(e) { // 设置开始时间
      this.searchData.startTime = e;
      this.endTimeOptions = {
        disabledDate: date => {
          const startTime = e ? new Date(e).valueOf() : null;
          return date && (date.valueOf() < startTime);
        }
      }
    },
    endTimeChange: function(e) { // 设置结束时间
      // 23:59:59
      this.searchData.endTime = e;
      const endTime = e ? new Date(e).valueOf() - 24 * 3600000 : null;
      this.startTimeOptions = {
        disabledDate(date) {
          return date && date.valueOf() > endTime;
        }
      }
    },
    // 自定义序号
    indexMethod(row) {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    },
    // 格式化日期
    formatDate(date) {
      const time = date ? this.$moment(date).format('YYYY年MM月DD日 HH:mm') : ''
      return time
    },
    // 分页
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.searchList()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.searchList()
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.isSelected = true;
      this.currentRow = currentRow
    },
    // 提交表单
    submitForm(name, form, method, callback) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show();
          that.commonUtil.ajaxCommonUtil(that.isCreate, method, form, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              callback()
            } else {
              that.$Message.error(res.msg);
            }
          }, () => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    // 删除
    deleteData(url, idName) {
      const that = this;
      const row = that.currentRow
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      if ([2, 3].indexOf(row.auditStatus) === -1) {
        that.$Message.warning('该审核状态下不能删除数据');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil(url + row[idName], (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.searchList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    // 审核情况详情
    getAuditDetail(h, params) {
      const row = params.row
      const status = row.auditStatus
      const audit = this.status[status]
      const buttons = [
        h('Button', {
          style: {
            color: audit.color + '!important'
          },
          props: {
            type: 'text',
            size: 'small'
          },
          on: {
            click: () => {
              const auditTime = this.formatDate(row.auditTime)
              if (status === 2 || status === 1) {
                // 赋值
                this.auditForm = {
                  // 审核人
                  auditMan: row.auditMan,
                  // 审核结果
                  result: audit,
                  // 审核意见
                  auditMessage: row.auditMessage,
                  auditTime: auditTime,
                  fileids: row.fileids
                }
                this.$refs.audit.displayAudit()
              } else {
                this.showCheck(row)
              }
            }
          }
        }, audit.name)
      ]
      // 如果是审核驳回的话，多添加一个按钮，再次提交
      if (status === 2) {
        buttons.push(h('Button', {
          style: {
            color: 'green'
          },
          props: {
            type: 'text',
            size: 'small'
          },
          on: {
            click: () => {
              this.showCheck(row)
            }
          }
        }, '再次提交'))
      }
      return h('div', buttons);
    },
    // 审核状态
    showCheck(row) {
      const that = this;
      if (row.auditStatus === 1) {
        that.$Message.error('项目已经审核通过，无需继续提交');
        return;
      }
      that.$Modal.confirm({
        title: '是否确认提交审核',
        content: '',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxPutUtil('/auditBatch/updateAuditBatch', { auditBatchId: row.auditBatchId, auditStatus: 0 }, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.searchList()
            } else {
              that.$Message.error(res.msg);
            }
          }, () => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      });
    },
    // 导出excel
    excelExcel(url) {
      const that = this;
      that.$Spin.show();
      that.commonUtil.downFile(url, {}, (res, fileName) => {
        // 关闭数据加载
        that.$Spin.hide();
        that.saveExcel(fileName, res)
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    saveExcel(name, res) {
      const blob = new Blob([res]);
      const link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      link.download = name
      // 此写法兼容可火狐浏览器
      document.body.appendChild(link);
      const evt = document.createEvent('MouseEvents');
      evt.initEvent('click', false, false);
      link.dispatchEvent(evt);
      document.body.removeChild(link);// 下载完成移除元素
    },
    // 打印
    printContent(type) {
      const query = {
        title: this.$route.meta.title,
        type: type
      }
      if (type === 18) {
        // 档案管理
        const that = this
        const selectRows = this.$refs.archivesList.getSelection()
        if (selectRows.length === 0) {
          that.$Message.warning('您未选择内容');
          return
        }
        const ids = selectRows.map(item => {
          return item.id
        }).join(',')
        query.ids = ids
      }
      const { href } = this.$router.resolve({
        path: '/printView',
        query: query
      })
      window.open(href, '_blank')
    },
    // 查看
    showInfo(row, modal) {
      this.clearData();
      this.form = Object.assign({}, row)
      // 禁用状态关闭
      this.isDisabled = true;
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
        this[modal] = true
      })
    }
  }
}



